body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: -webkit-gradient(linear, left bottom, right top, color-stop(25%, #313FA0), to(#6474D4));
  background: -webkit-linear-gradient(left bottom, #313FA0 25%, #6474D4);
  background: -o-linear-gradient(left bottom, #313FA0 25%, #6474D4);
  background: linear-gradient(to right top, #313FA0 25%, #6474D4);
}

@media (max-width: 600px) {
  body {
    background: -webkit-gradient(linear, left top, right top, from(#e0729c), to(#DF4380));
    background: -webkit-linear-gradient(left, #e0729c, #DF4380);
    background: -o-linear-gradient(left, #e0729c, #DF4380);
    background: linear-gradient(to right, #e0729c, #DF4380);
  }
}
html {
  height: 100%;
}



